@import url('//fonts.googleapis.com/css?family=Open+Sans:300,400,700');
@import url('//fonts.googleapis.com/css?family=Montserrat:400,700');

h1,h2,h3,h4,h5,h6{
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  color: #222;
}
h1{
  font-size: 2.8em;
}
h2{
  font-size: 1.7em;
  font-family: 'Montserrat';
  font-weight: bold;
}
h3{
  font-size: 1.8em;
}
h4{
  font-size: 1.5em;
}
h5{
  font-size: 1.05em;
  line-height: 1.6em;
  font-weight: bold;
  font-family: 'Montserrat'
}
h5 small{
  font-size: .9em;
  line-height: 1.6em;
}
