  display: flex;
  justify-content: space-between;
  color: #222;
}

body {
  background:#f8f8f8;
  font-family: 'Open Sans', sans-serif;
}

.App {
  text-align: center;
}



.cardexpander.expanded{
  position: fixed;
  z-index: 50;

}
.cardexpander{
  width: 280px;
}



.gluten-free,
.vegan,
.featured{
  display: inline-flex;
  height: 22px;
  color: #fff;
  margin-right: 10px;
  border-radius: 11px;
  align-items: center;
  justify-content: center;
  padding: 0 8px;
  font-weight: 400;
  font-size: .9em
}
.gluten-free{
  background: #3cc68a;
}
.vegan{
  background: #90EE90;
}
.featured{
  background: 	#8FBC8F;
}
.card-title small .MuiFormControl-root {
 margin-left:20px;
}
.MuiTypography-root .MuiFormControl-root{
  width: 100%;
  margin-top:20px;
}
.MuiTypography-root{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.cardexpander .MuiTouchRipple-root{
  display: none;
}

.cardexpander .MuiCardActionArea-root.Mui-focusVisible .MuiCardActionArea-focusHighlight{
  opacity: 0;
}
.cardexpander .MuiPaper-root{
  width: auto;
  min-width: 250px;
}
.cardexpander.expanded .MuiPaper-root{
  box-shadow: 0px 25px 45px 0px rgba(0, 0, 0, 0.17);
}
.cardexpander .MuiPaper-root:hover{
  transform: none;
}
.cardexpander.expanded .MuiPaper-root .MuiCardMedia-root{
  height: 200px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.headerBG{

    position: absolute;
    background: rgba(0,0,0,.5);
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    content: "";
    z-index: 5;

}
.headerTitle{
  z-index: 6;
}
.headerTitle h1{
  margin-bottom:0;
}
.headerTitle h6{
  margin-top:10px;
}


.App-link {
  color: #61dafb;
}

body{
  min-height:1500px;
}

.container{
  max-width: 90%;
  margin: 0 auto 0 auto;
  padding: 30px 0 0 0;
  display: block;
  position: relative;
}

.form{
  padding: 0 15px;
}

h1.page-title{
  padding: 30px 0;
  border-bottom: 1px solid #e8e8e8;
}

h3.title{
  margin: 30px 0 10px 0;
  text-align: left;
  color: #333;
}

.add-to-menu{
  padding: 0 0 40px 0;
  display: flex;
}

.add-to-menu .form{
  min-width: 400px;
  text-align: left;
  width: 100%;
  display: flex;
}
.add-to-menu .form>div{
  width: 50%;
}
.add-to-menu .form .MuiFab-extended{
  display: inline-flex;
  margin: 0 10px;
}
.add-to-menu .form .cardexpander{
  width: auto!important;
}
.add-to-menu .form .MuiPaper-root{
  margin: 0 auto 20px auto;
  display: block;
  width: 350px;
  
}
.add-to-menu .form .MuiPaper-root .MuiButtonBase-root{
  cursor: default;
}
.add-to-menu .form .MuiCard-root:hover{
  transform: none;
  box-shadow: 0px 18px 37px 0px rgba(0, 0, 0, 0.07)
}
.add-to-menu .MuiFormControl-root{
  margin-top: 10px;
  display: block;
  width: 100%;
}
#demo-simple-select-label{


  background: #fff;
  z-index: 10;
}

.section-form 
.add-to-menu .MuiInputBase-root{
  width: 100%;
  border-radius: 0;
 
}
.add-to-menu .MuiCardContent-root{
  min-height: 113px;
}
body .MuiPaper-elevation8{
  margin-top:55px;
}
.add-to-menu .MuiInputBase-root:hover,
.add-to-menu .MuiInputBase-root:focus,
.add-to-menu .MuiInputBase-root.Mui-focused,
.add-to-menu .MuiSelect-select:focus{
  background:rgba(255,255,255,0);
}
.add-to-menu .select-section .MuiInputLabel{
  transform: translate(0, 20px) scale(1);
}
.section-form .section-name,
.section-form .section-position{
  display: inline-flex;
  margin: 40px 0;
}
.section-form .section-name{
  padding-right: 30px;
  min-width: 250px;
}
.section-form .MuiFormControl-root{
  width: 100%;
}
body .section-form .section-position{
  width: 80px;
  padding-right: 30px;
}
#demo-simple-select-label.MuiInputLabel-shrink {
  transform-origin: top left;
}

.add-to-menu .MuiCardActions-root{
  display: none;
}
.MuiButtonBase-root.MuiFab-root{
  box-shadow: none;
}
.MuiCardActions-root a,
.section-form a
{
  font-size: .8rem;
  display: inline-flex;
  padding: 9px 10px;
  border-radius: 19px;
  color:#fff;
  font-size: 1.1em;
  margin-left: 7px;
  align-items: center;
}
.MuiCardActions-root{
  position: absolute;
  margin-top:8px;
}
.MuiCardActions-root a:hover,
.section-form a:hover
{
  background:rgba(255,255,255,.4);
}
.MuiCardActions-root a svg,
.section-form a svg{
  font-size: 1.3rem;
  margin-right: 5px;
}

body{
  background:#f5f5f5;
}

.grid{
  display: flex;
  flex-wrap: wrap;
  margin: 20px auto 50px auto;

}
.grid-container{
  max-width: 90%;
  margin: 50px auto;
  position: relative;
}

.MuiCardActionArea-root:hover .MuiCardActionArea-focusHighlight{
  opacity: 0!important;
}
.card-placeholder{
  width: 280px;
  min-height: 310px;
}
.section-underline{
  height: 3px;
  width: 350px;
  display: block;
  position: absolute;
  bottom: -10px;
  left: 0;
  background: linear-gradient(to right, #029b59 20%, #3cc68a 50%, #60d7a4 100%);
  content: "";
  border-radius: 0 30px 0 30px;
}
.section-header{
  display: flex;
  justify-content: space-between;
  width: 350px;
  align-items: center;

  .MuiFormControl-root{
    width: 100%;
  }
.section-actions{
  position: absolute;
  right: 0px;
  top: 0px;

  &.featured{
    display: none;
  }
}
  .MuiFab-root{
    height: 22px;
    width: 22px;
    min-height: 0;
    margin-left: 10px;
    svg{
      font-size: .8rem;
    }
    
  }
}
body .MuiCheckbox-root{
  padding: 9px 5px 9px 9px;
}
@media(max-width: 640px){
  .app-card-container{
    width: 100%;
  }
  .app-card-container form{
    width: 100%;
  }
  body .MuiCheckbox-root{
    padding: 9px 3px 9px 9px;
  }
  .hidden-sm{
    display: none;
  }
  .cardexpander{
    max-width: 100%;
    min-width: 100%;
    width: 100%;
  }
  .grid{
    position: relative;
  }
  .cardexpander .MuiPaper-root{
    min-width: 100%;
    width: 100%;
  }
  .cardexpander.expanded .MuiPaper-root{
    min-width: 0;
    width: 100%;
  }
  .card-placeholder{
    width: 100%;
    min-height: 310px;
    display: inline-flex;
  }
  .headerTitle{
    padding: 0 30px;
  }
  .grid-container h2{
    margin-left: 0!important;
  }
}













ul,
li {
  list-style: none;
  padding: 0;
  margin: 0;
}

.card-list {
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
  padding-top:30px;
}

.card {
  position: relative;
  padding: 15px;
  height: 380px;
  flex: 0 0 280px;
  max-width: 280px;
  cursor: pointer;
}



.card-content-container {
  width: 100%;
  height: 100%;
  position: relative;
  display: block;
  
}

.card-content-container.open {
  top: 0;
  left: 0;
  right: 0;
  position: fixed;
  z-index: 7;
  overflow-x: hidden;
  overflow-y: visible;
  padding: 40px 0;
  pointer-events: none;
}

.card-content {
  pointer-events: auto;
  position: relative;
  border-radius: 10px;
  background: #fff;
  overflow: hidden;
  width: 100%;
  height: 100%;
  margin: 0 auto;
}

.open .card-content {
  height: auto;
  max-width: 500px;
  overflow-x: hidden;
}

.card-open-link {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.card-image-container {
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
  height: 180px;
  width: 100vw;
  justify-content: flex-start;
  align-items: center;
  display: flex;

  .card-image-inner-container{
    width: 250px;
    overflow: hidden;
  }


  img{
    width: 500px;
    height: auto;
  }
}

.open .card-image-container,
.open .title-container {
  z-index: 7;
}

.title-container {
  position: absolute;
  top: 190px;
  left: 20px;
  max-width: calc(100% - 35px);
  text-align: left;
  height: 175px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  .tags{
    bottom: 0;
  }
}

.open .title-container {
  top: 190px;
  left: 20px;
  height: 0;


}

h2 {
  color: #333;
  margin: 8px 0;
  font-size: 1em;
  text-align: left;

  &.section-header{
    cursor: default;
    position: relative;
    text-align: left;
    margin: 40px 0 0 15px;
    font-size: 1.2em;
  }
}

.MuiFab-root{
  background: linear-gradient(120deg, darken(#1db675, 10%) 30%, darken(#60d7a4, 10%) 90%);
}

.react-parallax-content .MuiFab-root{
  margin: 0 15px;

  i{
    font-size: 1.1em;
    padding-left: 7px;
  }
}
.category {
  color: #333;
  font-size: 14px;
  text-align: left;
}

.overlay {
  z-index: 6;
  position: fixed;
  background: rgba(0, 0, 0, 0.8);
  will-change: opacity;
  top: 0;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
}
.no-pointer-events{
  pointer-events: none;
}
.overlay a {
  display: block;
  position: fixed;
  top: 0;
  bottom: 0;
  width: 100vw;
  left: 50%;

  transform: translateX(-50%);
}

.content-container {
  padding: 200px 20px 0px 20px;
  max-width: 460px;
  text-align: left;
  display: flex;
  flex-direction: column;
  overflow: visible;

  .sections-select{
    width: 100%;
    padding-right: 30px;

    div{
      display: flex;
      
    }
  }

  .title-inputs{
    display: flex;
    justify-content: space-between;

    &.column{
      flex-direction: column;
      min-width: 30%;

      &.wide{
        width: 60%;
      }
    }
  }
  .MuiInput-root{
    margin-bottom: 20px;
  }

  .card-buttons{
    padding: 15px 20px;
    margin: 20px -20px 0 -20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: #f1f1f1;

    .cancel-delete-buttons button{
      color: #333;
      display: inline-flex;
      border-radius: 20px;
      cursor: pointer;
      padding: 8px 20px;
      align-items: center;
      background-color: transparent;
      border-color: transparent;
      transition: 250ms;

      span{
        margin-left:5px;
      }

      &:hover{
        background: rgba(0,0,0,.08);
      }
      &:focus{
        outline: none;
        background: rgba(0,0,0,.2);
      }
    }
  }
}

p {
  color: #9d9ca1;
  font-size: 20px;
  line-height: 28px;
}

@media only screen and (max-width: 800px) {


}

@media only screen and (max-width: 600px) {
  .card {
    flex: 0 0 100%;
    max-width: 100%;
    padding: 10px;
    padding-left: 0;
    padding-right: 0;
  }

  .card-content-container.open {
    padding: 20px 0 0 0;
    pointer-events: none;
  }

}
